.exercises {
  font-family: $primaryFont;
  max-width: 90vw;
  margin: 0 auto;
  scroll-behavior: smooth;

  > h1 {
    font-size: 3rem;
    margin-bottom: 3rem;
  }

  > div {
    @include flex(center, center);
    flex-wrap: wrap;
    gap: 2rem;
  }
}

.exerciseCard {
  @include flex(center, flex-start);
  flex-direction: column;
  margin-bottom: 4rem;
  background-color: #fff;
  border-top: 4px solid $primaryColor;
  padding: 0 25px;
  text-decoration: none;
  font-family: $primaryFont;

  > div {
    margin: 1rem 0;

    button {
      color: #fff;
      border: none;
      margin-right: 1rem;
      text-transform: capitalize;
      letter-spacing: 1.5px;
      font-weight: 600;
      padding: 10px 15px;
      border-radius: 20px;

      &:first-of-type {
        background-color: #fda9ae;
      }

      &:last-of-type {
        background-color: #fbc85a;
      }
    }
  }

  h1 {
    color: #000;
    padding-bottom: 35px;
    text-transform: capitalize;
  }
}
